



























import service from '../../common/service.js'
export default {
	data() {
		return {
			active:1,
			money:'99',
			vipLevel:""
		};
	},
	methods:{
		change(index){
			if(this.vipLevel){
				if(this.vipLevel == 1){  
					this.active == 2;	
				}else if(this.vipLevel == 2){
					this.active != 2;	
				}	
			}else{
				this.active = index;
			}
			
			
		}
	},
	onLoad:function(){
		this.vipLevel = uni.getStorageSync('vipLevel');
		this.vipLevel = 1;
		//99元套餐
		if(this.vipLevel == 1){  
			this.active == 2;	
		}else if(this.vipLevel == 2){
			this.active == 1;	
		}
		
	}
}



